\r\n        vertical-align: middle;\r\n      }\r\n\r\n      // Separator styling\r\n      .marquee-item-separator {\r\n        display: inline-block;\r\n        margin-right: 4px;\r\n        vertical-align: middle;\r\n      }\r\n    }\r\n  }\r\n\r\n  // Fade mask system - only applied when classes are present\r\n  &.fade-mask-white {\r\n    &::before,\r\n    &::after {\r\n      content: ' ';\r\n      position: absolute;\r\n      z-index: 1;\r\n      pointer-events: none; // Allow clicks to pass through\r\n    }\r\n\r\n    // Vertical directions (up/down)\r\n    &:not(.horizontal) {\r\n      &::before,\r\n      &::after {\r\n        height: 12%;\r\n        width: 100%;\r\n      }\r\n\r\n      &::before {\r\n        background: linear-gradient(\r\n          180deg,\r\n          rgba(255, 255, 255, 0.8) 0%,\r\n          rgba(255, 255, 255, 0.5) 40%,\r\n          rgba(255, 255, 255, 0) 100%\r\n        );\r\n        top: 0;\r\n      }\r\n\r\n      &::after {\r\n        background: linear-gradient(\r\n          0deg,\r\n          rgba(255, 255, 255, 0.8) 0%,\r\n          rgba(255, 255, 255, 0.5) 40%,\r\n          rgba(255, 255, 255, 0) 100%\r\n        );\r\n        bottom: 0;\r\n      }\r\n    }\r\n\r\n    // Horizontal directions (left/right)\r\n    &.horizontal {\r\n      &::before,\r\n      &::after {\r\n        height: 100%;\r\n        width: 6%;\r\n      }\r\n\r\n      &::before {\r\n        background: linear-gradient(\r\n          90deg,\r\n          rgba(255, 255, 255, 0.8) 0%,\r\n          rgba(255, 255, 255, 0.5) 40%,\r\n          rgba(255, 255, 255, 0) 100%\r\n        );\r\n        left: 0;\r\n      }\r\n\r\n      &::after {\r\n        background: linear-gradient(\r\n          270deg,\r\n          rgba(255, 255, 255, 0.8) 0%,\r\n          rgba(255, 255, 255, 0.5) 40%,\r\n          rgba(255, 255, 255, 0) 100%\r\n        );\r\n        right: 0;\r\n      }\r\n    }\r\n  }\r\n\r\n  // Black fade mask variant\r\n  &.fade-mask-black {\r\n    &::before,\r\n    &::after {\r\n      content: ' ';\r\n      position: absolute;\r\n      z-index: 1;\r\n      pointer-events: none;\r\n    }\r\n\r\n    // Vertical directions (up/down)\r\n    &:not(.horizontal) {\r\n      &::before,\r\n      &::after {\r\n        height: 12%;\r\n        width: 100%;\r\n      }\r\n\r\n      &::before {\r\n        background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 40%, rgba(0, 0, 0, 0) 100%);\r\n        top: 0;\r\n      }\r\n\r\n      &::after {\r\n        background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 40%, rgba(0, 0, 0, 0) 100%);\r\n        bottom: 0;\r\n      }\r\n    }\r\n\r\n    // Horizontal directions (left/right)\r\n    &.horizontal {\r\n      &::before,\r\n      &::after {\r\n        height: 100%;\r\n        width: 6%;\r\n      }\r\n\r\n      &::before {\r\n        background: linear-gradient(90deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 40%, rgba(0, 0, 0, 0) 100%);\r\n        left: 0;\r\n      }\r\n\r\n      &::after {\r\n        background: linear-gradient(270deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.3) 40%, rgba(0, 0, 0, 0) 100%);\r\n        right: 0;\r\n      }\r\n    }\r\n  }\r\n\r\n  // Horizontal marquee layout (regardless of fade mask)\r\n  &.horizontal {\r\n    .marquee {\r\n      align-items: center;\r\n      display: flex;\r\n      height: 100%;\r\n      white-space: nowrap;\r\n      width: auto;\r\n      min-width: 100%;\r\n\r\n      &.left-scroll {\r\n        justify-content: flex-end;\r\n      }\r\n\r\n      &.right-scroll {\r\n        justify-content: flex-start;\r\n      }\r\n    }\r\n\r\n    .marquee-item {\r\n      flex-shrink: 0;\r\n      white-space: nowrap;\r\n      padding: 0 10px;\r\n    }\r\n  }\r\n\r\n  // Vertical marquee layout - simple positioning, no flexbox\r\n  &.vertical {\r\n    .marquee {\r\n      height: auto;\r\n      min-height: 100%;\r\n      width: 100%;\r\n    }\r\n\r\n    .marquee-item {\r\n      padding: 10px 0;\r\n    }\r\n  }\r\n}\r\n"],"sourceRoo